@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Quicksand:wght@300;400;500;600;700&display=swap);
.list-result ul {
  width: 100%;
  list-style: none;
  /* display: block; */
}

.list-result ul .top-ten-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list-result ul .show-btn {
  background-color: #222;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 25px;
  cursor: pointer;
  font-size: 1rem;
}

.list-result ul li {
  width: 600px;
  padding: 0;
  padding-left: 15px;
  margin-bottom: 8px;
  background-color: #fff;
  border-radius: 5px;
  font-weight: 700;
}

.list-result ul li.error-list {
  padding: 10px;
  padding-left: 15px;
}

.single-result {
  /* max-width: fit-content; */
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  padding: 20px;
}

.result-information {
  padding-right: 20px;
}

.result-information .title {
  margin-bottom: 15px;
  font-size: 1.8rem;
}

.result-information p {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.result-image {
  border-left: 1px #ccc solid;
  padding-left: 20px;
}

.result-image img {
  border-radius: 10px;
}

.firstLoadingOverlay {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  color: #fff;
  background-color: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 500;
}

.firstLoadingOverlay p {
  margin-top: 10px;
}

.app {
  width: 100%;
  height: 100vh;
  display: flex;

  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 0 auto;
  padding: 50px;
  background-size: cover;
  background-image: linear-gradient(rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.85)),
    url(/country-finder/static/media/world.58df30e0.jpg);
}

.app h1 {
  color: #fff;
  margin-bottom: 30px;
}

.form-container {
  max-width: 900px;
  padding: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ccc;
  border-radius: 10px;
}

.form-container label {
  font-weight: 600;
  font-size: 1.2rem;
  margin-right: 15px;
}

.form-container input {
  /* width: 100%; */
  font-size: 1.2rem;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}

.form-container form {
  margin-bottom: 15px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #1b1b1b;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

